import React, { useState } from "react";
import { useHistory } from "react-router-dom";
import PropTypes from "prop-types";
import { useTranslation } from "react-i18next";
import { useModal } from "mui-modal-provider";
import Grid from "@mui/material/Grid";
import Button from "@mui/material/Button";
import AuthService from "../services/AuthService";
import TokenService from "../services/TokenService";
import { TabContainer, TabBodyScrollable, TabTitle, TabParagraph, TabPrevButton, TabNextButton } from "./TabsComponents";
import FlexibleDialog from "./FlexibleDialog";
//import config from "../config";



function Tab07Publish(props) {
console.log('07pub');
  const { t } = useTranslation();
  const history = useHistory();
  const [ prevIsEnabled, ] = useState(true);
  const [ nextIsEnabled, setNextIsEnabled ] = useState(() => props.job?.download ? props.job?.downloadDataset : false);
  const { showModal } = useModal();
  const openDialog = (props) => showModal(FlexibleDialog, props);
console.log("UUUUU:", props.job.transform.metadati.urlFile);
  const publish_url = props.job.transform.metadati.urlFile;

  const onPrev = () => {
    props.goto("prev");
  };

  const onNext = () => {
    props.goto("next");
  };

  const userIsAuthenticated = () => {
    if (!AuthService.getCurrentUser()) { // user is not authenticated
      openDialog({
        title: t("Please log in or register"),
        contentText: t("You need to be authenticated to proceed"),
        actions: [
          {
            text: t("Login"),
            closeModal: true,
            autoFocus: true,
            callback: () => {
              TokenService.set("redirect", props.tabId);
              history.push("/signin");
            },
          },
          {
            text: t("Register"),
            closeModal: true,
            callback: () => {
              TokenService.set("redirect", props.tabId);
              history.push("/signup");
            },
          },
          {
            text: t("Cancel"),
            closeModal: true,
            callback: () => props.goto("prev"),
          }
        ],
      });
      return false;
    }
    return true;
  }

  async function onVerifyPublished() { 
    if (userIsAuthenticated()) {
      let datasetIsPublished = await exists(publish_url); // TODO ...
      setNextIsEnabled(true);
      props.setJob({...props.job, datasetIsPublished: datasetIsPublished});
    }
  };

  async function exists(url) {
    url = "https://www.google.com";
try {
  let res = await makeRequest("GET", url);
  console.info("res:", res);
} catch(e) {
  console.error("ERR:", e);
}
    // const result = await fetch(url, { method: 'HEAD' });
    // console.log("exists", url, result, result.ok);
    // return result.ok;
  }

  function makeRequest(method, url) {
    return new Promise(function (resolve, reject) {
        let xhr = new XMLHttpRequest();
        xhr.open(method, url);
        xhr.onload = function () {
            if (this.status >= 200 && this.status < 300) {
                resolve(xhr.response);
            } else {
                reject({
                    status: this.status,
                    statusText: xhr.statusText
                });
            }
        };
        xhr.onerror = function () {
            reject({
                status: this.status,
                statusText: xhr.statusText
            });
        };
        xhr.send();
    });
}
  return (
    <TabContainer>
      <TabBodyScrollable>
        <TabTitle>
          {t("Publish downloaded dataset")}
        </TabTitle>
        <TabParagraph>
          <p>
            {t("Provide to publish downloaded dataset")}.
          </p>
          <p>
            {t("The file will have to be reachable at address")} <a href={publish_url}>{publish_url}</a>
          </p>
        </TabParagraph>
        <TabParagraph>
          <Button onClick={onVerifyPublished} variant="contained" color="tertiary">
            {t("Verify publication")} 🌍
          </Button>
        </TabParagraph>
      </TabBodyScrollable>

      <Grid container>
        <Grid item xs={6}>
          <TabPrevButton onPrev={onPrev} prevIsEnabled={prevIsEnabled}>
            {t("Back")}
          </TabPrevButton>
        </Grid>
        <Grid item xs={6}>
          <TabNextButton onNext={onNext} nextIsEnabled={nextIsEnabled}>
            {t("Continue")}
          </TabNextButton>
        </Grid>
      </Grid>

    </TabContainer>
  );
}
Tab07Publish.propTypes = {
  goto: PropTypes.func.isRequired,
};
Tab07Publish.defaultProps = {
};

export default React.memo(Tab07Publish);
